import {Div,Panel, PanelHeader, Placeholder, Button} from "@vkontakte/vkui";
import React, {useState} from "react";
import '../App.css';

//_______ICONS_________________
import Icon56AddCircleOutline from '@vkontakte/icons/dist/56/add_circle_outline';
import Icon56CheckCircleOutline from '@vkontakte/icons/dist/56/check_circle_outline';

function PanelOneStarter(props) {
  let [texts, setTexts] = useState(
      props.isComplete
          ? 'complete'
          : 'notComplete'
  );
  let textData = {
    complete:{
      header: 'Подкаст добавлен',
      action: 'Поделиться подкастом',
      placeholder: 'Раскажите своим подписчикам о новом подкасте, чтобы получить больше слушателей.',
      icon: <Icon56CheckCircleOutline/>,
      onClick: ''
    },
    notComplete: {
      header: 'Добавьте первый подкаст',
      action: 'Добавить подкаст',
      placeholder: 'Добавляйте, редактируйте и делитесь подкастами вашего сообщества.',
      icon: <Icon56AddCircleOutline/>,
      onClick: 'newPodcast'
    }
  }

  return (
      <Panel id={props.id}>
        <PanelHeader left={props.left}>
          <Div style={{margin: '0 auto'}}>Подкасты</Div>
        </PanelHeader>

        <Placeholder
            stretched
            icon= {textData[texts]['icon']}
            header={textData[texts]['header']}
            action={<Button size="l" onClick={props.goForward}>{textData[texts]['action']}</Button>}
        >
          {textData[texts]['placeholder']}
        </Placeholder>
      </Panel>
  )
}

export default PanelOneStarter;
