import {Card, CardScroll, Group} from "@vkontakte/vkui";
import React from "react";


function Editor(props) {
  // console.log('Audio',props.audio.audioData);
  return (
      <Group separator="hide" description="" style={{margin: '24px 0'}}>
        <CardScroll>
          <Card size="l" style={{width: '3500px'}}>
            <div style={{height: 26}}/>
            <div style={{height: 96}}/>
          </Card>
        </CardScroll>
      </Group>
  )
}

export default Editor
