import {
  Placeholder, Div, Textarea, Panel, PanelHeader,
  Button, FormLayout, FormLayoutGroup, Input, Separator,
  Checkbox, Group, RichCell,
  FixedLayout, File
} from "@vkontakte/vkui";
import React, {useState} from "react";
import '../App.css';

//_______ICONS_________________
import Icon56GalleryOutline from '@vkontakte/icons/dist/56/gallery_outline';
import Icon28ChevronRightOutline from '@vkontakte/icons/dist/28/chevron_right_outline';

function PanelOneStarter(props) {
  // console.log('Audio', props.audio.audioData);
  const [data, setData] = useState({
    isFormValid: false,
    name: '',
    about: '',
    audioFile: '',
    notNormal: false,
    notExport: false,
    trailer: false,
    auditory: '',
    next: false,
  });

  const [picture, setPicture] = useState(null);
  const [imgData, setImgData] = useState(null);
  const onChangePicture = e => {
    if (e.target.files[0]) {
      // console.log("picture: ", e.target.files);
      setPicture(e.target.files[0]);
      const reader = new FileReader();
      reader.addEventListener("load", () => {
        setImgData(reader.result);
      });
      reader.readAsDataURL(e.target.files[0]);
    }
  };

  return (

      <Panel id={props.id}>
        <PanelHeader left={props.left}>
          <Div style={{margin: '0 auto'}}>Новый подкаст</Div>
        </PanelHeader>
        <Div style={{display: 'flex'}}>
          <File
              type="file"
              mode="secondary"
              onChange={onChangePicture}
              style={{
                width: '72px',
                height: '72px',
                backgroundSize: 'cover',
                backgroundPosition: 'center center',
                backgroundImage: (imgData
                    ? `url(${imgData})`
                    : null),
              }}
          >
            {imgData
                ? null
                : <Icon56GalleryOutline width={25} height={25}
                                        style={{top: '14px', left: '6px', position: 'relative'}}/>}
          </File>
          {/*<img className="playerProfilePic_home_tile" src={imgData} />*/}
          <FormLayout style={{marginTop: '-12px', flexGrow: 1}}>
            <FormLayoutGroup
                top="Название"
                bottom={(false !== false ? "Это поле обязательное" : null)}
                status={data.isFormValid ? 'valid' : 'error'}
            >
              <Input
                  onChange={(event)=>{
                    let newData = data;
                    newData.name = event.target.value;
                    setData(newData)
                  }}
                  type="text"
                  placeholder="Введите название подкаста"/>
            </FormLayoutGroup>
          </FormLayout>
        </Div>

        <FormLayout style={{margin: '-12px 0', flexGrow: 1}}>
          <Textarea
              onChange={(event)=>{
                let newData = data;
                newData.about = event.target.value;
                setData(newData)
              }}
              style={{height: '64px'}}
              top="Описание подкаста"/>
        </FormLayout>

        <Placeholder header="Загрузите Ваш подкаст"
                     action={<File mode="outline">Загрузить файл</File>}>
          Выберите готовый аудиофайл из вашего телефона и добавьте его
        </Placeholder>

        <Separator style={{margin: '-12px 0'}}/>

        <FormLayout>
          <FormLayoutGroup>
            <Checkbox
            onChange={(event)=>{
                    let newData = data;
                    newData.notNormal = event.target.value;
                    setData(newData)
                  }}
            >Ненормативный контент</Checkbox>
            <Checkbox
            onChange={(event)=>{
                    let newData = data;
                    newData.notExport = event.target.value;
                    setData(newData)
                  }}
            >Исключить эпизод из экспорта</Checkbox>
            <Checkbox
            onChange={(event)=>{
                    let newData = data;
                    newData.trailer = event.target.value;
                    setData(newData)
                  }}
            >Трейлер подкаста</Checkbox>
          </FormLayoutGroup>
        </FormLayout>


        <Group
            style={{margin: '-12px 0'}}
            description="При публикации записи с эпизодом, он становится доступным для всех пользователей"
        >
          <RichCell
              caption="Всем пользователям"
              after={<Icon28ChevronRightOutline style={{color: 'gray'}}/>}>
            Кому доступен данный подкаст
          </RichCell>
        </Group>

        <FixedLayout vertical="bottom" style={{background: 'white'}}>
          <Div style={{display: 'flex'}}>
            <Button
                size="l"
                stretched
                onClick={props.goForward}
            >Далее</Button>
          </Div>
        </FixedLayout>

      </Panel>
  )
}

export default PanelOneStarter;
