import React, {useState} from 'react';
import './App.css';
import bridge from "@vkontakte/vk-bridge";
//_________COMPONENTS________________________
import {
  Root, View,
} from "@vkontakte/vkui";
//_________ICONS____________________
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
// import PanelHeaderBack from "index";
//____________Panels_________________
import PanelOneStarter from './Panels/PanelOneStarter'
import PanelOneNewPodcast from './Panels/PanelOneNewPodcast'
import PanelOneEdit from './Panels/PanelOneEdit'

//_________Lets Move On________________________


function App(props) {

  let [user, setUser] = useState(null);
  bridge.send('VKWebAppGetUserInfo', {vk_user_id: ''})
      .then((user) => {
        setUser({user});
      });

  let [activePanel, setActivePanel] = useState('main');
  let [history, setHistory] = useState(['main']);
  let [data, setData] = useState({
    isComplete: false //TODO complete function
  });

  const updateData = (name, value) => {
    let newData = {...data};
    newData[name] = value;
    setData(newData);
    console.log('Update. Name: ', name, " Value ", value)
  }
  const goBack = () => {
    const story = [...history];
    story.pop();
    const activePanel = story[story.length - 1];
    setHistory(story);
    setActivePanel(activePanel);
  }
  const goForward = (activePanel) => {
    const story = [...history];
    story.push(activePanel);
    setHistory(story);
    setActivePanel(activePanel);
  }

  //______________________left options_________________
  const backBtn = <Icon24BrowserBack onClick={goBack}/>;
//______________________________________________________
  return (
      <Root activeView="active">
        <View id="active" activePanel={activePanel}>{/*  activePanel  'newPodcast'  */}
          <PanelOneStarter
              id={"main"}
              left={null}
              updateData={updateData}
              goForward={()=>{goForward('newPodcast')}}
              isComplete={data.isComplete}
          />
          <PanelOneNewPodcast
              id={"newPodcast"}
              left={backBtn}
              updateData={updateData}
              goForward={()=>{goForward('edit')}}
          />
          <PanelOneEdit
              id={"edit"}
              left={backBtn}
              updateData={updateData}
              goForward={()=>{goForward('NEXTTTTT')}}
          />
        </View>
      </Root>)
}

