import {
  Div, Panel, PanelHeader,
  FormLayout, FormLayoutGroup, Input,
  Group, Header,
  CellButton, Card, CardScroll
} from "@vkontakte/vkui";
import React, {useState} from "react";
import '../App.css';
import './Editor.css';
import Editor from './Editor'

//_______ICONS_________________
import Icon28AddCircleOutline from '@vkontakte/icons/dist/28/add_circle_outline';
import Icon28RemoveCircleOutline from '@vkontakte/icons/dist/28/remove_circle_outline';

function PanelOneEdit(props) {
  let [data, setData] = useState({
    timecodes: [

    ],
    isFormValid: false,
    loadImg: '',
    name: '',
    about: '',
    audioFile: '',
    notNormal: false,
    notExport: false,
    trailer: false,
    auditory: '',
    next: false,
  });
  const validation = (value, rules) => {

  };
  const updateData = (name, value) => {
    let newData = {...data};
    newData[name] = value;
    setData(newData);
    // console.log('Update. Name: ', name, " Value ", value)
  }

  const addItemToList = () => {
    let listData = data.timecodes.slice();
    listData.push({value: 'Новый таймкод', time: '00:00:00'});
    updateData('timecodes', listData);
  }

  const renderTimecodes = () => {
    let listData = [...data.timecodes];
    return listData.map((timecode, index) => {
      return (
          <Div
              style={{display: 'flex', margin: '0'}}
              key={index}
          >
            <CellButton
                onClick={() => {
                  listData.splice(index, 1);
                  updateData('timecodes', listData);
                }}
                before={<Icon28RemoveCircleOutline style={{color: 'red'}}/>}
                style={{padding: 0, margin: 0, width: 'auto'}}/>
            <Input
                onChange={(event) => {
                  updateData(timecode.value, event.target.value);
                }}
                type="text"
                placeholder={timecode.value}/>
            <Input
                onChange={(event) => {
                  updateData(timecode.time, event.target.value);
                }}
                type="text"
                placeholder={timecode.time}
                style={{width: '90px'}}/>
          </Div>
      )
    })
  };

  return (
      <Panel id={props.id}>

        <PanelHeader left={props.left}>
          <Div style={{margin: '0 auto'}}>Редактирование</Div>
        </PanelHeader>

        <Editor/>

        <Group
            header={<Header mode="secondary">Таймкоды</Header>}
            description="Отметки времени с названием темы. Позволяют слушателям легче путешествовать по подкасту."
        >
          <FormLayout
              style={
                (data.timecodes.length !== 0
                    ? {display: 'block'}
                    : {display: 'none'}
                )}
          >
            <FormLayoutGroup style={{display: 'flex', justifyContent: 'space-between'}}>
              <Group>
                {renderTimecodes()}
              </Group>
            </FormLayoutGroup>
          </FormLayout>
          <CellButton
              before={<Icon28AddCircleOutline wight={24} height={24}/>}
              onClick={addItemToList}
          >
            Добавить таймкод
          </CellButton>
        </Group>

      </Panel>
  )
}

export default PanelOneEdit;
